.Facture-header h1 {
  text-align: center;
  font-size: 3.5rem;
  padding-bottom: 1em;
  border-bottom: 1px solid gray;
  font-weight: bold;
  color: #04295d;
}

.button {
  padding: 1.5em;
  margin: 1em;
  font-weight: bold;
  border-radius: 1em;
  background-color: #04295d;
  color: white;
  width: 220px;
  height: 170px;
}

.Facture {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 6em;
  margin-top: 5em;
}

.facture_button:hover {
  background-color: #2fbfff;

  border: solid 4px #3ebde6;
}
